
















































import { Component, DialogSection } from "sitewhere-ide-common";

import DialogForm from "../common/form/DialogForm.vue";
import FormText from "../common/form/FormText.vue";
import FormSelect from "../common/form/FormSelect.vue";

import { required, sameAs, minLength } from "vuelidate/lib/validators";

@Component({
  components: {
    DialogForm,
    FormText,
    FormSelect
  },
  validations: {
    username: {
      required
    },
    password: {
      required,
      minLength: minLength(6)
    },
    passwordConfirm: {
      sameAsPassword: sameAs("password")
    },
    firstName: {
      required
    },
    lastName: {
      required
    },
    status: {
      required
    }
  }
})
export default class TenantDetailFields extends DialogSection {
  username: string | null = null;
  password: string | null = null;
  passwordConfirm: string | null = null;
  firstName: string | null = null;
  lastName: string | null = null;
  status: string | null = null;

  // Content for dropdowns.
  statuses: { text: string; value: string }[] = [
    {
      text: "Active",
      value: "Active"
    },
    {
      text: "Expired",
      value: "Expired"
    },
    {
      text: "Locked",
      value: "Locked"
    }
  ];

  /** Reset section content */
  reset(): void {
    this.username = null;
    this.password = null;
    this.passwordConfirm = null;
    this.firstName = null;
    this.lastName = null;
    this.status = null;
    this.$v.$reset();
  }

  /** Perform validation */
  validate(): boolean {
    this.$v.$touch();
    return !this.$v.$invalid;
  }

  /** Load form data from an object */
  load(input: {}): void {
    this.username = (input as any).username;
    this.password = (input as any).password;
    this.firstName = (input as any).firstName;
    this.lastName = (input as any).lastName;
    this.status = (input as any).status;
  }

  /** Save form data to an object */
  save(): {} {
    return {
      username: this.username,
      password: this.password,
      firstName: this.firstName,
      lastName: this.lastName,
      status: this.status
    };
  }
}
