












import { Component, Prop } from "sitewhere-ide-common";
import Vue from "vue";

@Component({})
export default class FilterChip extends Vue {
  @Prop() type!: string;
  @Prop() token!: string;
  @Prop() imageUrl!: string;
  @Prop() label!: string;
  @Prop() tooltip!: string;

  /** Emit when filter is closed */
  onFilterClosed() {
    this.$emit("closed", this.type, this.token);
  }
}
