

















































import {
  Component,
  ListComponent,
  IPageSizes,
  ITableHeaders,
  Refs
} from "sitewhere-ide-common";

import ActionsBlock from "../common/ActionsBlock.vue";
import UserCreateDialog from "./UserCreateDialog.vue";
import UserUpdateDialog from "./UserUpdateDialog.vue";
import UserDeleteDialog from "./UserDeleteDialog.vue";

import { NavigationIcon } from "../../libraries/constants";
import { formatDate } from "../common/Utils";
import { AxiosPromise } from "axios";

import { listUsers } from "../../rest/sitewhere-users-api";
import {
  IUser,
  IUserSearchCriteria,
  IUserResponseFormat,
  IUserSearchResults
} from "sitewhere-rest-api";

@Component({
  components: {
    ActionsBlock,
    UserCreateDialog,
    UserUpdateDialog,
    UserDeleteDialog
  }
})
export default class UsersList extends ListComponent<
  IUser,
  IUserSearchCriteria,
  IUserResponseFormat,
  IUserSearchResults
> {
  $refs!: Refs<{
    add: UserCreateDialog;
    edit: UserUpdateDialog;
    delete: UserDeleteDialog;
  }>;

  headers: ITableHeaders = [
    {
      align: "left",
      sortable: false,
      text: "User Name",
      value: "username"
    },
    {
      align: "left",
      sortable: false,
      text: "First Name",
      value: "firstname"
    },
    {
      align: "left",
      sortable: false,
      text: "Last Name",
      value: "lastname"
    },
    {
      align: "left",
      sortable: false,
      text: "Status",
      value: "status"
    },
    {
      align: "left",
      sortable: false,
      text: "Created",
      value: "created"
    },
    {
      align: "left",
      sortable: false,
      text: "Updated",
      value: "updated"
    },
    {
      align: "left",
      sortable: false,
      text: "Actions",
      value: "actions"
    }
  ];
  pageSizes: IPageSizes = [
    {
      text: "25",
      value: 25
    },
    {
      text: "50",
      value: 50
    },
    {
      text: "100",
      value: 100
    }
  ];

  /** Get page icon */
  get icon(): NavigationIcon {
    return NavigationIcon.User;
  }

  /** Build search criteria for list */
  buildSearchCriteria(): IUserSearchCriteria {
    let criteria: IUserSearchCriteria = {};
    return criteria;
  }

  /** Build response format for list */
  buildResponseFormat(): IUserResponseFormat {
    let format: IUserResponseFormat = {};
    return format;
  }

  /** Perform search */
  performSearch(
    criteria: IUserSearchCriteria,
    format: IUserResponseFormat
  ): AxiosPromise<IUserSearchResults> {
    return listUsers(this.$store, criteria, format);
  }

  // Called to open dialog.
  onAddUser() {
    this.$refs.add.open();
  }

  // Called to open edit dialog.
  onEditUser(username: string) {
    this.$refs.edit.open(username);
  }

  // Called to open delete dialog.
  onDeleteUser(username: string) {
    this.$refs.delete.open(username);
  }

  // Format a date.
  formatDate(date: Date) {
    formatDate(date);
  }
}
